.scroll-bg{
    background-color: white;
    width: 600px;
    margin-top: 5px;
    border-radius: 10px;
    
    
 }


 .scroll-div{
    width: 600px;
    background-color: white;
    height: 100px;
    overflow: hidden;
    overflow-y: scroll;
    border-radius: 10px;
    box-shadow: 0 0.2rem 0.3rem rgba(0, 0, 0, 0.616);
    
 }

 .scroll-object{
    color: #ff416e;
    font-size: 36px;
    overflow-y: scroll;

 }
 .transcriptheading{
    font-size: 15px;
    color: #ff416e;
    padding: 5px;
    text-align: justify;
 }

 .optionlanguage{
    display: flex;
    justify-content: center;
    text-align: center;
    margin-left: 16rem;
    margin-top: 1rem;
 }